          text: "Rafa Brando  um cantor e compositor brasileiro com uma longa trajetria musical iniciada nos anos 1980 em Belo Horizonte, sua cidade natal. Comeou como vocalista em diversos grupos e, nos anos 1990, no Rio de Janeiro  onde viveu por muitos anos , consolidou-se como letrista e compositor ao integrar as bandas Vitrine e Radicais Livres. Nos Radicais Livres, estabeleceu uma parceria com Ronaldo Miragaya, que durou at sua sada do Brasil no final de 2002.\n\nEm 2001, os Radicais Livres lanaram um lbum homnimo, no qual se destacou a cano Ataques, vencedora do concurso Ih Radical, do canal Multishow, com mais de 22 mil votos pela internet. O prmio garantiu  banda um videoclipe produzido pelo prprio Multishow e grande visibilidade na mdia, com entrevistas e matrias na imprensa.\n\nEntre 2003 e 2012, Rafa viveu em Lisboa, Portugal, onde recomeou sua carreira, tocando com diversos msicos brasileiros e portugueses e iniciando apresentaes em bares no formato voz e violo.\n\nDesde 2012, reside em Hamburgo, Alemanha, onde, em 2018, lanou seu primeiro lbum individual, Solo, no qual escreveu praticamente todas as letras e msicas. O disco foi produzido e arranjado por seu amigo, o guitarrista e compositor Jess Gomes. Suas influncias musicais vo da msica popular brasileira, bossa nova, samba, pop e rock  black music americana, incluindo soul, funk e blues. Entre suas grandes referncias, destacam-se Caetano Veloso, Cazuza e Mick Jagger.\n\nDesde 2018, Rafa realiza concertos anuais com sua banda, apresentando composies prprias, alm de desenvolver um projeto em duo com o percussionista Vito Cardoso. Tambm mantm o formato acstico de voz e violo, apresentando-se em festas privadas e em locais de destaque, como o restaurante brasileiro Xod, em Hamburgo, onde toca todas as quartas-feiras desde julho de 2023.\n\nO lbum Solo, de Rafa Brando, est disponvel nas principais plataformas digitais, como Spotify, Deezer, Apple Music e Amazon Music, alm de ser vendido em suas apresentaes. O lbum Radicais Livres (2001) tambm pode ser encontrado no Spotify.\n\nCompositor atento s diversidades do mundo e aos sinais espirituais, letrista essencialmente urbano, que captura em suas canes as nuances das relaes humanas, dos amores e desencontros, e, acima de tudo, um cantor que ama cantar e levar sua msica a todos os cantos do planeta.\n\nApresento-lhes, Rafa Brando.",
